import router from './router'
import store from './store'
import NProgress from 'nprogress' // Progress 
import axios from 'axios'
import 'nprogress/nprogress.css' // Progress 
import {
    Message
} from 'element-ui'
import {
    getAuth
} from '@/utils/auth' // 

let loadAsyncRouter = false
const whiteList = ['/login', '/welcome'] // 
router.beforeEach((to, from, next) => {
    if (to.meta.disabled) {
        next(false)
        return
    }
    NProgress.start()
        /**     */
    if (getAuth()) {
        
        if (whiteList.includes(to.path)) {
            next({
                path: '/'
            })
            NProgress.done()

        } else {
            // if (!loadAsyncRouter) { // 
            //     loadAsyncRouter = true

            //     if (store.getters.allAuth) {
            //         store.dispatch('GenerateRoutes', store.getters.allAuth).then(() => { // auths
            //             router.addRoutes(store.getters.addRouters) // 

            //             if (to.path === '/404') {
            //                 next({
            //                     path: to.redirectedFrom || '/',
            //                     replace: true
            //                 })
            //             } else {
            //                 next({
            //                     ...to,
            //                     replace: true
            //                 })
            //             }
            //         })
            //     } else {
            //         store.dispatch('getAuth').then(auths => { // user_info
            //             store.dispatch('GenerateRoutes', auths).then(() => { // auths
            //                 router.addRoutes(store.getters.addRouters) // 
            //                 if (to.path === '/404') {
            //                     next({
            //                         path: to.redirectedFrom || '/',
            //                         replace: true
            //                     })
            //                 } else {
            //                     next({
            //                         ...to,
            //                         replace: true
            //                     })
            //                 }
            //             })
            //         }).catch((err) => {
            //             loadAsyncRouter = false
            //             store.dispatch('LogOut').then(() => {
            //                 Message.error(err.msg || '')
            //                 next({
            //                     path: '/'
            //                 })
            //             })
            //         })
            //     }
            // } else {
            //     next()
            // }
            next()
        }
    } else {
        if (whiteList.indexOf(to.path) !== -1) {
            next()
        } else {
            next(`/login`) // 
            NProgress.done()
        }
    }
})

router.afterEach(() => {
    NProgress.done() // Progress
})

router.onError((error) => {
    const pattern = /Loading chunk (\d)+ failed/g
    const isChunkLoadFailed = error.message.match(pattern)
    const targetPath = router.history.pending.fullPath
    if (isChunkLoadFailed) {
        router.replace(targetPath)
    }
})


